@import url('./_config.css');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'montserrat', sans-serif;
}

.app {
	background-size: cover;
	background-position: top;
	transition: all 0.4s ease-out;
}

.app.warm {
	background-image: url('./assets/warm-bg.jpg');
}

.app.cold {
	background-image: url('./assets/cold-bg.jpg');
}

main {
	min-height: 100vh;
	background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.8));
	padding: 25px;
}

.search-box {
	width: 100%;
	margin: 0 0 75px;
}

.search-box .search-bar {
	display: block;
	width: 100%;
	padding: 15px;

	appearance: none;
	background: none;
	border: none;
	outline: none;

	background-color: rgba(255, 255, 255, 0.5);
	border-radius: 0 0 16px 16px;
	margin-top: -25px;
	box-shadow: 0 5px rgba(0, 0, 0, 0.2);

	color: #313131;
	font-size: 20px;

	transition: 0.4s ease;
}

.search-box .search-bar:focus {
	background-color: rgba(0, 0, 0, 0.75);
	color: #e1e1e1;
}

.location-box .location {
	color: #fff;
	font-size: 32px;
	font-weight: 500;
	text-align: center;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
	color: #fff;
	font-size: 20px;
	font-weight: 300;
	font-style: italic;
	text-align: center;
	text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
	text-align: center;
}

.weather-box .temp {
	position: relative;
	display: inline-block;
	margin: 30px auto;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 16px;

	padding: 15px 25px;

	color: white;
	font-size: 100px;
	font-weight: 900;

	text-shadow: 3px 10px 2px rgba(50, 50, 70, 0.5);
	text-align: center;
	box-shadow: 0px 6px 4px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
	color: white;
	font-size: 48px;
	font-weight: 700;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

h1.not-found {
	text-align: center;

	font-size: 50px;
	font-weight: 700;

	color: white;
	text-shadow: 4px 4px 1px rgba(0, 0, 0, 0.2);
}

footer {
	text-align: center;
	padding: 1rem;
	background: rgba(0, 0, 0, 0.9);
	color: white;
	height: 50px;
}

@media screen and (min-width: 392px) {
	.app.cold {
		background-image: url('./assets/cold-bg-xl.jpg');
	}
	.app.warm {
		background-image: url('./assets/warm-bg-xl.jpg');
	}

	.search-box .search-bar {
		width: 50%;
		margin: auto;
		margin-top: -25px;
	}

	h1.not-found {
		font-size: 100px;
	}
}
